@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.search-block {
    align-items: flex-end;
}

.main-input {
    font-size: 1.3rem;
}

.main-label {
    font-weight: 500;
    margin-bottom: 0px;
}

a.btn {
    font-size: 0.85rem;
    border-radius: 1px;
    border: 1px solid #833AE0;
    padding: 0.75rem 2rem;
}

a.btn-primary {
    background-color: #833AE0;
    transition: all 0.2s linear;
}

a.btn-primary:hover {
    transition: all 0.2s linear;
    background-color: #c86adfcc;
    opacity: 0.8;
    border: none;
}
.btn a {
    color: #FFFFFF;
    text-decoration: none;
}

.adv-input {
    font-weight: 300;
    font-size: 14px;
    text-decoration: underline;
}

.arrow {
    text-decoration: none;
    margin-left: 7px;
    transition: ease 0.4s;
}

.hide {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: ease all 0.4s;
}

.adv-input:hover {
    cursor: pointer;
}

.advanced-filter {
    display: flex;
    margin-bottom: 10px;
    box-sizing: border-box;
}

img .card-img-bottom {
    width: 214px;
    height: 214px;
}

.card-link {
    color: #000000;
    text-decoration: none;
}

.card-link:hover {
    cursor: pointer;
}

body .card {
    border: none;
}
body .card-footer {
    position: fixed;
    width: 100%;
    bottom: 0;
}
.App {
  font-family: 'Roboto', sans-serif;
}
.collapse span {
  margin-right: 2rem;
}

.navbar-text svg:hover {
  cursor: pointer;
}

.navbar-text:hover {
  font-weight: 900;
  text-decoration: underline;
}

.navbar a {
  color: black;
  text-decoration: none;
}

.navbar .navbar-brand {
  font-size: calc(1.475rem + 2.7vw);
}
